.m-header {
  display: flex;
  flex-direction: row;
  /*user-select: none;*/
  .m-header__logo {
    clear: both;
    line-height: 30px;
    color: #444;
    .m-header__logoSide {
      float: left;
    }
    .m-header__logoMain {
      overflow: hidden;
    }
    .txt {
      font-size: 26px;
      line-height: 55px;
      vertical-align: middle;
    }
    .version {
      font-size: 14px;
      vertical-align: middle;
    }
    .u-icon-logo-m3 {
      font-size: 50px;
      line-height: 1;
      color: #444;
      vertical-align: middle;
      margin-right: 14px;
    }
  }
  .m-header__show {
    flex: 1;
    text-align: right;
    font-size: 14px;
    line-height: 30px;
    color: #666;
    .m-header__zoom {
      display: inline-block;
      line-height: 30px;
      text-align: left;
      vertical-align: middle;
      .u-icon-zoomin,
      .u-icon-zoomout {
        font-size: 20px;
        line-height: 30px;
      }
    }
    .m-header__rotate {
      position: relative;
      display: inline-block;
      line-height: 50px;
      text-align: left;
      margin-left: 30px;
      padding-right: 30px;
      vertical-align: middle;
      .u-icon-document,
      .u-icon-document-h {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%) rotate(0deg);
        font-size: 24px;
        line-height: 30px;
        &.z-horizontal {
          transform: translateY(-50%) rotate(90deg);
        }
      }
    }
    .number {
      margin-left: 3px;
      margin-right: 3px;
    }
  }
}

.m-canvas {
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
  padding: 30px;
  outline: 1px dashed #ccc;
  background-color: #fff;
  &:hover {
    .u-corner-page {
      visibility: visible;
      opacity: 1;
      transition: 0.5s;
    }
  }
  .m-canvas__title {
    font-size: 30px;
    line-height: 1.5;
    text-align: left;
    padding-bottom: 30px;
    .ttl {
      font-size: 30px;
      line-height: 1.5;
      height: 45px;
      outline: 1px dashed #ccc;
    }
  }
  .m-canvas__area {
    position: relative;
    outline: 1px dashed #ccc;
    &.m-canvas__material {
      &:hover {
        .u-corner-material {
          visibility: visible;
          opacity: 1;
          transition: 0.5s;
        }
      }
      .m-imageList {
        li {
        }
      }
    }
    &.m-canvas__result {
      &:hover {
        .u-corner-result {
          visibility: visible;
          opacity: 1;
          transition: 0.5s;
        }
      }
      .m-imageList {
        li {
        }
      }
    }
  }
  .u-corner {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    padding: 0 10px;
    background-color: rgba(200, 200, 100, 0.2);
    font-size: 12px;
    line-height: 1.6;
    color: #666;
  }
}

.m-imageList {
  overflow: hidden;
  clear: both;
  min-height: 30px;
  padding-top: 30px;
  li {
    box-sizing: border-box;
    position: relative;
    float: left;
    font-size: 0;
    line-height: 0;
    &.z-clearLeft {
      clear: left;
    }
    .m-imageList__cell {
      position: relative;
      outline: 1px dashed #ddd;
      text-align: center;
    }
    .img {
      width: 100%;
    }
    .txt {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      transform: translateY(100%);
      white-space: normal;
      text-align: center;
      font-size: 14px;
      line-height: 1.5;
      color: #333;
      text-shadow: 1px 1px 0 #fff;
    }
    &:hover {
      outline-color: #ccc;
      .u-icon-closeb {
        visibility: visible;
        opacity: 1;
        transition: opacity 0.3s;
        transition-delay: 0.1s;
      }
    }
    .u-icon-closeb {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      right: 0;
      top: 0;
      transform: translate(50%, -50%);
      width: 20px;
      height: 20px;
      background-color: rgba(200, 200, 100, 0.5);
      font-size: 14px;
      color: #333;
      line-height: 20px;
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
    }
  }
}

